body {
  background-color: rgb(30, 30, 30);
  color: rgb(180, 187, 188);
  position: relative;
  min-height: 100vh;
  font-family: consolas, monaco, monospace;
}

.header {
  position: absolute;
  top: 0;
  width: 100%;
  background-color: rgb(60, 60, 60);
  height: 3rem;
}

.name-logo {
  float: left;
  padding-top: 1rem;
}

.name-logo > a {
  text-decoration: none;
  font-size: large;
  color:rgb(180, 187, 188);
}

.sidebar {
  position: absolute;
  left: 0;
  top: 3rem;
  bottom: 2rem;
  width: 8rem;
  background-color: #111;
  font-size: large;
  background-color: rgb(37, 37, 38);
}

.nav-item {
  border-style: solid;
  border-width:  2px 0 0 0;
  border-color: rgb(46, 46, 46);
}

.nav-item > a {
  text-decoration: none;
  font-weight: bold;
  padding-left: 0.5rem;
  vertical-align: 25%;
  color: rgb(180, 187, 188);
}

.nav-item > a:hover {
  text-decoration: underline;
  color: rgb(191, 197, 19);
}

.regina-picture {
  margin: 0 2rem;
  max-width: 15rem;
  max-height: 15rem;
  border-radius: 1%;
  margin-bottom: 1rem;
  filter: grayscale();
}

.bio {
  margin-left: 1rem;
}

.bio > h2{
  color: rgb(73, 190, 85);
}

.section {
  padding-top: 3rem;
  padding-bottom: 2rem;
  margin-left: 8rem;
  min-height: 100%;
  color: 	rgb(35, 170, 242);
  background-color: rgb(30, 30, 30);
}

.about {
  padding-top: 2rem;
  display: flex;
  width: 100%;
  border-color: rgb(46, 46, 46);
  border-style: solid;
  border-width: 2px 0 0 0;
}

.about-me {
  list-style: none;
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 2rem;
  background-color: rgb(0, 122, 204);
}

.footer > span {
  position: absolute;
  right: 1rem;
  bottom: .5rem;
  margin-right: 1rem;
}

.project {
  text-align: center;
  padding-left: 3rem;
  padding-right: 3rem;
  padding-bottom: 2rem;
  border-color: rgb(46, 46, 46);
  border-style: solid;
  border-width: 0 0 2px 0;
}

.project-description {
  max-width: 800px;
}

.video {
  max-width: 800px;
}

.repos {
  list-style: inside;
}

/* iframe css found here: https://www.benmarshall.me/responsive-iframe-generator/ */
.iframe-container {
  overflow: hidden;
  padding-top: 56.25%;
  position: relative;
}

.iframe-container iframe {
  border: 0;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

.contact-links {
  float: right;
  padding-right: .5rem
}

.social-media {
  float: left;
}

.sm-logo {
  padding-left: 1rem;
  padding-top: .5rem;
  height: 32px;
  width: auto;
}

.large-logo {
  height: 64px;
  width: auto;
}

.large-linkedin {
  height: 56px;
  width: auto;
}

.white {
  filter: brightness(0) invert(1);
}

.resume {
  padding-left: 4rem;
  padding-top: 8rem;
  
}

.contact {
  padding-top: 3rem;
  padding-bottom: 2rem;
  text-align: center;
}

.contact h1, h2 {
  color: 	rgb(35, 170, 242);
}

label, button {
  display: block;
  margin-left: 35%;
  text-align: left;
}

input, textarea {
  width: 30%;  
}

textarea {
  height: 8rem;
}

.email-button {
  /* float: left; */
  margin-bottom: 5%;
  padding: 10px;
  /* display: inline-block;
  text-align: center; */
  /* border-radius: 100px; */
  /* text-decoration: none; */
  /* text-transform: uppercase; */
  /* width: width-button; */
  cursor: pointer;
  background: -webkit-linear-gradient(#17EAD9, #5B247A);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.social-media-contacts {
  margin-left: 29%;
}

.other-contacts {
  padding-bottom: 10rem;
  border-top: 1px solid rgba(180, 187, 188, 0.212);
}


@media only screen and (max-width: 700px) {

  .resume {
    padding-left: 0;
  }

  .bio > h2 {
    font-size: 1em;
  }

  .contact {
    padding-left: 30%;
  }

  input, textarea {
    width: 75%;  
  }

  label, button {
    display: block;
    margin-left: 12%;
    text-align: left;
  }

  .regina-picture {
    margin-left: 0;
  }
  .project-description {
    overflow-wrap: break-word;
  }

  .about {
    margin-left: 1rem;
    width: 75%;
    display: block;
  }

  .large-logo {
    height: 16px;
    width: auto;
  }
  
  .large-linkedin {
    height: 14px;
    width: auto;
  }

}


